
"use client";

import React from 'react'; // Import React
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { useSpreadsheet } from "@/hooks/useSpreadsheet";
import { OfflineSheetLogo } from "../icons/OfflineSheetLogo";
import Link from "next/link";
import { Save, Home, Upload, Download, Undo, Redo, Bold, Italic, Underline, AlignLeft, AlignCenter, AlignRight, FileText, RowsIcon, Columns, Trash2 } from "lucide-react"; // Added Trash2, RowsIcon, Columns
import {
  Menubar,
  MenubarContent,
  MenubarItem,
  MenubarMenu,
  MenubarSeparator,
  MenubarShortcut,
  MenubarTrigger,
  MenubarGroup,
} from "@/components/ui/menubar"
import { Separator } from "@/components/ui/separator"; 
import { cn } from '@/lib/utils';

interface ToolbarProps {
  spreadsheetName: string;
}

export default function Toolbar({ spreadsheetName }: ToolbarProps) {
  const { 
    saveSpreadsheet, 
    spreadsheet, 
    setSpreadsheet, 
    selectedCell, 
    updateSelectedCellStyle,
    insertRow,
    deleteRow,
    insertColumn,
    deleteColumn,
    undo,
    redo,
    canUndo,
    canRedo
  } = useSpreadsheet();

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (spreadsheet && setSpreadsheet) {
      setSpreadsheet(prev => prev ? ({ ...prev, name: e.target.value, updatedAt: Date.now() }) : null);
    }
  };

  const selectedCellStyle = React.useMemo(() => {
    if (!spreadsheet || !selectedCell) return {};
    const sheet = spreadsheet.sheets.find(s => s.id === selectedCell.sheetId);
    if (!sheet) return {};
    const cell = sheet.cells[selectedCell.rowIndex]?.[selectedCell.colIndex];
    return cell?.style || {};
  }, [spreadsheet, selectedCell]);

  const handleToggleBold = () => {
    if (selectedCell) updateSelectedCellStyle({ bold: !selectedCellStyle?.bold });
  };
  const handleToggleItalic = () => {
    if (selectedCell) updateSelectedCellStyle({ italic: !selectedCellStyle?.italic });
  };
  const handleToggleUnderline = () => {
    if (selectedCell) updateSelectedCellStyle({ underline: !selectedCellStyle?.underline });
  };

  const handleExport = (format: 'xlsx' | 'csv') => console.log(`Export as ${format} (Not implemented)`);
  const handleLoadFromFile = () => console.log("Load from file (Not implemented)");
  
  const handleInsertRow = () => {
    if (spreadsheet && selectedCell) {
      insertRow(selectedCell.sheetId, selectedCell.rowIndex);
    } else if (spreadsheet && spreadsheet.sheets.length > 0) {
      // If no cell selected, insert at the beginning of the active sheet
      const activeSheetId = spreadsheet.activeSheetId;
      insertRow(activeSheetId, 0);
    }
  };
  const handleDeleteRow = () => {
    if (spreadsheet && selectedCell) {
      deleteRow(selectedCell.sheetId, selectedCell.rowIndex);
    }
  };
  const handleInsertColumn = () => {
     if (spreadsheet && selectedCell) {
      insertColumn(selectedCell.sheetId, selectedCell.colIndex);
    } else if (spreadsheet && spreadsheet.sheets.length > 0) {
      const activeSheetId = spreadsheet.activeSheetId;
      insertColumn(activeSheetId, 0);
    }
  };
  const handleDeleteColumn = () => {
    if (spreadsheet && selectedCell) {
      deleteColumn(selectedCell.sheetId, selectedCell.colIndex);
    }
  };
  
  const setTextAlign = (align: 'left' | 'center' | 'right') => {
    if (selectedCell) updateSelectedCellStyle({ textAlign: align });
  };


  return (
    <div className="p-2 border-b bg-card flex flex-col md:flex-row items-start md:items-center justify-between gap-2 shadow-sm flex-wrap print:hidden">
      {/* Top row: Logo, Name, Menus */}
      <div className="flex items-center gap-2 w-full md:w-auto">
        <Link href="/" aria-label="Back to dashboard">
          <OfflineSheetLogo className="h-8 w-8"/>
        </Link>
        <Input 
          value={spreadsheetName} 
          onChange={handleNameChange}
          className="font-semibold text-lg w-auto min-w-[150px] max-w-[300px]"
          aria-label="Spreadsheet Name"
          disabled={!spreadsheet}
        />
         <Menubar className="border-none shadow-none p-0 h-auto ml-auto md:ml-2">
          <MenubarMenu>
            <MenubarTrigger className="px-3 py-1.5">File</MenubarTrigger>
            <MenubarContent>
              <MenubarItem onClick={saveSpreadsheet} disabled={!spreadsheet}><Save className="mr-2 h-4 w-4" /> Save</MenubarItem>
              <MenubarItem onClick={handleLoadFromFile}><Upload className="mr-2 h-4 w-4" /> Load from File...</MenubarItem>
              <MenubarSeparator />
              <MenubarItem onClick={() => handleExport('xlsx')}><Download className="mr-2 h-4 w-4" /> Export as .xlsx</MenubarItem>
              <MenubarItem onClick={() => handleExport('csv')}><FileText className="mr-2 h-4 w-4" /> Export as .csv</MenubarItem>
              <MenubarSeparator />
              <MenubarItem asChild><Link href="/"><Home className="mr-2 h-4 w-4" /> Dashboard</Link></MenubarItem>
            </MenubarContent>
          </MenubarMenu>

          <MenubarMenu>
            <MenubarTrigger className="px-3 py-1.5">Edit</MenubarTrigger>
            <MenubarContent>
              <MenubarItem onClick={undo} disabled={!canUndo}><Undo className="mr-2 h-4 w-4" /> Undo <MenubarShortcut>Ctrl+Z</MenubarShortcut></MenubarItem>
              <MenubarItem onClick={redo} disabled={!canRedo}><Redo className="mr-2 h-4 w-4" /> Redo <MenubarShortcut>Ctrl+Y</MenubarShortcut></MenubarItem>
              <MenubarSeparator />
              <MenubarItem onClick={handleInsertRow} disabled={!spreadsheet}><RowsIcon className="mr-2 h-4 w-4" /> Insert Row Above</MenubarItem>
              <MenubarItem onClick={handleDeleteRow} disabled={!selectedCell}><Trash2 className="mr-2 h-4 w-4" /> Delete Row</MenubarItem>
              <MenubarSeparator />
              <MenubarItem onClick={handleInsertColumn} disabled={!spreadsheet}><Columns className="mr-2 h-4 w-4" /> Insert Column Left</MenubarItem>
              <MenubarItem onClick={handleDeleteColumn} disabled={!selectedCell}><Trash2 className="mr-2 h-4 w-4" /> Delete Column</MenubarItem>
              {/* <MenubarSeparator />
              <MenubarItem onClick={mergeCells}><Combine className="mr-2 h-4 w-4" /> Merge Cells</MenubarItem> */}
            </MenubarContent>
          </MenubarMenu>

          <MenubarMenu>
            <MenubarTrigger className="px-3 py-1.5">Format</MenubarTrigger>
            <MenubarContent>
              <MenubarGroup>
                  <MenubarItem onClick={handleToggleBold} disabled={!selectedCell} className={cn(selectedCellStyle?.bold && "bg-accent")}>
                    <Bold className="mr-2 h-4 w-4" /> Bold
                  </MenubarItem>
                  <MenubarItem onClick={handleToggleItalic} disabled={!selectedCell} className={cn(selectedCellStyle?.italic && "bg-accent")}>
                    <Italic className="mr-2 h-4 w-4" /> Italic
                  </MenubarItem>
                  <MenubarItem onClick={handleToggleUnderline} disabled={!selectedCell} className={cn(selectedCellStyle?.underline && "bg-accent")}>
                    <Underline className="mr-2 h-4 w-4" /> Underline
                  </MenubarItem>
              </MenubarGroup>
              <MenubarSeparator />
              <MenubarMenu>
                  <MenubarTrigger className="w-full justify-start font-normal px-2 py-1.5 text-sm relative" disabled={!selectedCell}>
                      <AlignLeft className="mr-2 h-4 w-4" /> Align Text
                  </MenubarTrigger>
                  <MenubarContent>
                      <MenubarItem onClick={() => setTextAlign('left')} className={cn(selectedCellStyle?.textAlign === 'left' && "bg-accent")}><AlignLeft className="mr-2 h-4 w-4" /> Left</MenubarItem>
                      <MenubarItem onClick={() => setTextAlign('center')} className={cn(selectedCellStyle?.textAlign === 'center' && "bg-accent")}><AlignCenter className="mr-2 h-4 w-4" /> Center</MenubarItem>
                      <MenubarItem onClick={() => setTextAlign('right')} className={cn(selectedCellStyle?.textAlign === 'right' && "bg-accent")}><AlignRight className="mr-2 h-4 w-4" /> Right</MenubarItem>
                  </MenubarContent>
              </MenubarMenu>
            </MenubarContent>
          </MenubarMenu>
        </Menubar>
      </div>

      {/* Second row: Quick action buttons (simulating a simplified ribbon) */}
      <div className="flex items-center gap-1 flex-wrap mt-2 md:mt-0 border-t md:border-t-0 pt-2 md:pt-0 w-full">
        {/* Group: File/Edit Actions */}
        <Button variant="ghost" size="icon" onClick={saveSpreadsheet} aria-label="Save Spreadsheet" title="Save" disabled={!spreadsheet}><Save /></Button>
        <Button variant="ghost" size="icon" onClick={undo} aria-label="Undo" title="Undo" disabled={!canUndo}><Undo /></Button>
        <Button variant="ghost" size="icon" onClick={redo} aria-label="Redo" title="Redo" disabled={!canRedo}><Redo /></Button>
        
        <Separator orientation="vertical" className="h-6 mx-1"/>
        
        {/* Group: Insert/Delete Actions - Quick Buttons */}
        <Button variant="ghost" size="icon" onClick={handleInsertRow} aria-label="Insert Row Above" title="Insert Row Above" disabled={!spreadsheet}><RowsIcon /></Button>
        <Button variant="ghost" size="icon" onClick={handleDeleteRow} aria-label="Delete Row" title="Delete Row" disabled={!selectedCell}><Trash2 /></Button> {/* Re-using Trash for row delete for now */}
        <Button variant="ghost" size="icon" onClick={handleInsertColumn} aria-label="Insert Column Left" title="Insert Column Left" disabled={!spreadsheet}><Columns /></Button>
        <Button variant="ghost" size="icon" onClick={handleDeleteColumn} aria-label="Delete Column" title="Delete Column" disabled={!selectedCell}><Trash2 /></Button> {/* Re-using Trash for col delete */}


        <Separator orientation="vertical" className="h-6 mx-1"/>

        {/* Group: Font Actions */}
        <Button 
          variant={selectedCellStyle?.bold ? "secondary" : "ghost"} 
          size="icon" 
          onClick={handleToggleBold} 
          aria-label="Bold" 
          title="Bold"
          disabled={!selectedCell}
        >
          <Bold />
        </Button>
        <Button 
          variant={selectedCellStyle?.italic ? "secondary" : "ghost"} 
          size="icon" 
          onClick={handleToggleItalic} 
          aria-label="Italic" 
          title="Italic"
          disabled={!selectedCell}
        >
          <Italic />
        </Button>
        <Button 
          variant={selectedCellStyle?.underline ? "secondary" : "ghost"} 
          size="icon" 
          onClick={handleToggleUnderline} 
          aria-label="Underline" 
          title="Underline"
          disabled={!selectedCell}
        >
          <Underline />
        </Button>

        <Separator orientation="vertical" className="h-6 mx-1"/>
        
        {/* Group: Alignment Actions */}
        <Button 
          variant={selectedCellStyle?.textAlign === 'left' ? "secondary" : "ghost"} 
          size="icon" 
          onClick={() => setTextAlign('left')} 
          aria-label="Align Left" 
          title="Align Left"
          disabled={!selectedCell}
        >
          <AlignLeft />
        </Button>
        <Button 
          variant={selectedCellStyle?.textAlign === 'center' ? "secondary" : "ghost"} 
          size="icon" 
          onClick={() => setTextAlign('center')} 
          aria-label="Align Center"
          title="Align Center" 
          disabled={!selectedCell}
        >
          <AlignCenter />
        </Button>
        <Button 
          variant={selectedCellStyle?.textAlign === 'right' ? "secondary" : "ghost"} 
          size="icon" 
          onClick={() => setTextAlign('right')} 
          aria-label="Align Right" 
          title="Align Right"
          disabled={!selectedCell}
        >
          <AlignRight />
        </Button>
        
      </div>
    </div>
  );
}
